@use "modules/core";
@use "modules/components/soul-skeleton";

$_skeleton-text-last-line-width: 80%;

:host {
  display: block;
  width: 100%;
  max-width: 100%;
}

.soul-skeleton-text {
}

.soul-skeleton-text__line {
  @include soul-skeleton.soul-skeleton-element();

  background-color: core.$soul-color-illustration-light;

  + .soul-skeleton-text__line {
    margin: core.$soul-space-stack-top-xs;

    &:last-of-type {
      width: $_skeleton-text-last-line-width;
    }
  }
}

@media screen and (prefers-reduced-motion: no-preference) {
  .soul-skeleton-text__line {
    @include soul-skeleton.soul-skeleton-shimmer-animation();
  }
}
